* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container-form {
  width: 100vw;
  height: 60px;
  background-color: #3457b4;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  box-shadow: 2px 2px 4px 1px #42445a;
}

input {
  width: 250px;
  height: 32px;
  background-color: #fff;
  border: none;
  padding-left: 10px;
}

.btn {
  height: 32px;
  width: 32px;
  background-color: #f1eff1;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  transform: translateX(-100%);
}

.icon {
  fill: #888;
  width: 30px;
  height: 20px;
  background-color: #f1eff1;
  border: none;
  margin-left: 1px;
}

